* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

.app {
	width: 100vw;
	height: 100vh;
	display: flex;
	align-items: center;
	justify-content: center;
}

.board {
	min-width: 300px;
	min-height: 400px;
	border: 5px solid lightgray;
	padding: 20px 10px;
	border-radius: 20px;
	margin: 10px;
	display: flex;
	align-items: center;
	flex-direction: column;
}

.board_title {
	font-size: 28px;
}

.item {
	width: 100%;
	border: 2px solid lightpink;
	padding: 18px;
	border-radius: 5px;
	margin: 5px 0;
	cursor: grab;
	background-color: #fcfcf9;
}
